<template>
  <section>
    <div class="gray-box">
      <p class="title">      .</p>
      <p class="text">                .</p>
    </div>

    <div class="basic-list">
      <dl>
        <dt>1.     ?</dt>
        <dd>
          <multiselect class="multiselect-basic" :id="'investYr'" :title="''" v-model="investYr" :options="investYrs" :onClose="selectClose" v-validate="'required'" data-vv-name="" />
        </dd>
        <dt>2.     .</dt>
        <dd>
          <multiselect class="multiselect-basic" :id="'stockSelect'" :title="''" v-model="stockSelect" :options="stockSelects" :onClose="selectClose" v-validate="'required'" data-vv-name="" />
        </dd>
        <dt>3.      ?</dt>
        <dd>
          <multiselect class="multiselect-basic" :id="'riskLimit'" :title="''" v-model="riskLimit" :options="riskLimits" :onClose="selectClose" v-validate="'required'" data-vv-name="" />
        </dd>
        <dt>4.       ?</dt>
        <dd>
          <multiselect class="multiselect-basic" :id="'holdPeriod'" :title="''" v-model="holdPeriod" :options="holdPeriods" :onClose="selectClose" v-validate="'required'" data-vv-name="" />
        </dd>
        <dt>5.   ? (1 )</dt>
        <dd>
          <input type="text" v-model="stockProfit"> %
        </dd>
        <dt>6.   ? ()</dt>
        <dd>
          <multiselect class="multiselect-basic" :id="'yrProfit'" :title="''" v-model="yrProfit" :options="yrProfits" :onClose="selectClose" v-validate="'required'" data-vv-name="" />
        </dd>
        <dt>7.      ? (1 )</dt>
        <dd>
          <multiselect class="multiselect-basic" :id="'investTime'" :title="''" v-model="investTime" :options="investTimes" :onClose="selectClose" v-validate="'required'" data-vv-name="" />
        </dd>
        <dt>8.  ?</dt>
        <dd>
          <multiselect class="multiselect-basic" :id="'losscut'" :title="''" v-model="losscut" :options="losscuts" :onClose="selectClose" v-validate="'required'" data-vv-name="" />
        </dd>
      </dl>
    </div>

    <div class="btn-wrap float">
      <a @click="saveInvestSurvey()" class="blue box solid"></a>
    </div>

  </section>
</template>

<script>
export default {
  name: "investSurvey",
  data() {
    return {
      investYrs: [
        { text: "", value: "" },
        { text: "6", value: "11" },
        { text: "6~1", value: "12" },
        { text: "1~3", value: "13" },
        { text: "3~5", value: "14" },
        { text: "5~10", value: "15" },
        { text: "10~20", value: "16" },
        { text: "20", value: "17" }
      ],
      investYr: "",
      stockSelects: [
        { text: "", value: "" },
        { text: "  ", value: "18" },
        { text: "", value: "19" },
        { text: " ", value: "20" }
      ],
      stockSelect: "",
      riskLimits: [
        { text: "", value: "" },
        { text: "", value: "21" },
        { text: " 0~1%", value: "22" },
        { text: " 1~1%", value: "23" },
        { text: " 5~10%", value: "24" },
        { text: " 10~20%", value: "25" },
        { text: " 20~30%", value: "26" },
        { text: " 30~50%", value: "27" },
        { text: " 50~80%", value: "28" },
        { text: " 80%", value: "29" }
      ],
      riskLimit: "",
      holdPeriods: [
        { text: "", value: "" },
        { text: "1", value: "30" },
        { text: "1~3", value: "31" },
        { text: "3~1", value: "32" },
        { text: "1~2", value: "33" },
        { text: "2~1", value: "34" },
        { text: "1~3", value: "35" },
        { text: "3~6", value: "36" },
        { text: "6~1", value: "37" },
        { text: "1~2", value: "38" },
        { text: "2~5", value: "39" },
        { text: "5~10", value: "40" },
        { text: "10", value: "41" }
      ],
      holdPeriod: "",
      yrProfits: [
        { text: "", value: "" },
        { text: "3%", value: "42" },
        { text: "3~5%", value: "43" },
        { text: "5~10%", value: "44" },
        { text: "10~20%", value: "45" },
        { text: "20~30%", value: "46" },
        { text: "30%", value: "47" }
      ],
      yrProfit: "",
      investTimes: [
        { text: "", value: "" },
        { text: "1", value: "48" },
        { text: "1~2", value: "49" },
        { text: "2~5", value: "50" },
        { text: "5~20", value: "51" },
        { text: "20~40", value: "52" }
      ],
      investTime: "",
      losscuts: [
        { text: "", value: "" },
        { text: "", value: "53" },
        { text: "5%", value: "54" },
        { text: "10%", value: "55" },
        { text: "20%", value: "56" },
        { text: "50%", value: "57" },
        { text: "", value: "58" }
      ],
      losscut: "",
      stockProfit: ""
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = " ";
  },
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    validate: function() {
      var _this = this;

      if (_this.investYr == null || "" == _this.investYr.value) {
        _this.$toast.center("  ");
        return false;
      } else if (_this.stockSelect == null || "" == _this.stockSelect.value) {
        _this.$toast.center("  ");
        return false;
      } else if (_this.riskLimit == null || "" == _this.riskLimit.value) {
        _this.$toast.center("  ");
        return false;
      } else if (_this.holdPeriod == null || "" == _this.holdPeriod.value) {
        _this.$toast.center("  ");
        return false;
      } else if ("" == _this.stockProfit) {
        _this.$toast.center("  ");
        return false;
      } else if (_this.yrProfit == null || "" == _this.yrProfit.value) {
        _this.$toast.center("  ");
        return false;
      } else if (_this.investTime == null || "" == _this.investTime.value) {
        _this.$toast.center("  ");
        return false;
      } else if (_this.losscut == null || "" == _this.losscut.value) {
        _this.$toast.center("  ");
        return false;
      }
      return true;
    },
    saveInvestSurvey: function() {
      var _this = this;
      if (!_this.validate()) return false;

      console.log("investYr=" + _this.investYr.value);

      var formData = new FormData();
      formData.append("investYr", _this.investYr.value);
      formData.append("stockSelect", _this.stockSelect.value);
      formData.append("riskLimit", _this.riskLimit.value);
      formData.append("holdPeriod", _this.holdPeriod.value);
      formData.append("stockProfit", _this.stockProfit);
      formData.append("yrProfit", _this.yrProfit.value);
      formData.append("investTime", _this.investTime.value);
      formData.append("losscut", _this.losscut.value);

      this.$http
        .post("/m/diags/saveInvestSurvey.json", formData)
        .then(response => {
          this.$router.push("/assets/diagsStart");
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    nextOpen: function(option) {
      this.$refs.category.open();
    },
    selectClose: function(option) {}
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



